* {
  box-sizing: border-box;
  font-family: Georgia, "Times New Roman", Times, serif;
}

html,
body,
#root {
  padding: 0;
  margin: 0;
  height: 100%;
}

.header {
  width: 100%;
  text-align: center;
  color: black;
}

.wrapper {
  padding-top: 50px;
  background-image: url("https://i2.wp.com/onlyvectorbackgrounds.com/wp-content/uploads/2018/10/Abstract-Gradient-Shadow-Lines-Background-Orange.jpg?fit=1191%2C843");
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  text-align: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.card {
  background: #fff;
  position: relative;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
  margin-top: 10px;
}

.card > .img-container {
  overflow: hidden;
  text-align: center;
  background: white;
}
.card > .img-container > img {
  height: 250px;
  width: 250px;
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}

.scoreSummary {
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
  color: white;
}

